<!--new arrivals--><section class="new-arrivals" v-if="arrivalData"><div class="container"><div class="main-title"><h2>{{ $t("new_arrivals") }}</h2><div class="decore-title"><img src="@/assets/images/favicon.png" alt=""></div></div><div class="content"><carousel class="arrivals-carousel" :items-to-show="4" :autoplay="5000" :settings="settings" :breakpoints="breakpoints" dir="ltr"><slide class="arrivals-box" v-for="product in arrivalData" :key="product.id"><div class="image"><a href="single-product.html"> <img class="img-fluid" :src="product.image" :alt="product.image_alt"></a><div class="fav" v-if="userData"><form enctype="multipart/form-data" @submit.prevent="removeFromWishlist"><input name="product_id" :value="product.id" type="hidden"><button class="btn remove" type="submit"><i class="bi bi-heart"></i></button></form></div><div class="layer" v-if="userData"><a class="btn" href="#!">{{ $t("add_to_cart") }}</a></div></div><div class="details"><div class="title"><h3><a href="single-product.html">{{ product.title }}</a></h3></div></div></slide><template #addons><Navigation></Navigation></template></carousel></div></div></section><!--new arrivals-->